    --font-family-1: Muli;

    --p-color1: #707070;
    --p-color2: #000000;
    --p-color3: #FFFFFF;
    --p-color4: #A9C5BA;

    --links-text-decoration: underline;

    --letter-spacing: 0px;


    /* CONTENT FORM */

    --content-form-background: #FFFFFF 0% 0% no-repeat padding-box;

    /* BUTTON SIGN UP */

    --button-sign-up-background: #707070 0% 0% no-repeat padding-box;
    --button-sign-up-border-radius: 100px;
    --button-sign-up-opacity: 1;

    --button-sign-up-letter-spacing: 0px;
    --button-sign-up-color: #FFFFFF;
    --button-sign-up-opacity: 1;


    /* BUTTON GOOGLE */

    --button-google-box-shadow: 0px 3px 6px #00000029;
    --button-google-border-radius: 100px;
    --button-google-opacity: 1;

  }
  

