.content-signin{
    width: 50%;
}

.content-signin h1 {
    margin: 120px 15% auto auto;
    text-align: right;
    font: normal normal 900 35px/48px Muli;
    font: var(--font-35-900);
}


.content-signin .forms{
    margin: 70px auto auto auto;
    max-width: 450px;
}

.content-signin .forms section h2 {
    margin-top: 50px;
    text-align: center;
    font: normal normal normal 30px/38px Muli;
    font: var(--font-30-normal);
    color: #707070;
    color: var(--color1);
}

