    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.content-signin .forms form label{
    margin-top: 20px;
    font: normal normal normal 14px/20px Muli;
    font: var(--font-14-normal);
    color: #707070;
    color: var(--color1);
}

.content-signin .forms form input{
    margin-top: 10px;
    font: normal normal normal 14px/20px Muli;
    font: var(--font-14-normal);
    border: 0;
    border-bottom: 1px solid;
    padding: 0px 0px 8px 20px;
}

.content-signin .forms form a{
    text-align: right;
    margin-top: 20px;
    text-decoration: none;
    color: #707070;
    color: var(--color1);
    font: normal normal normal 14px/20px Muli;
    font: var(--font-14-normal);
}

.content-signin .forms form button{
    margin: 40px auto auto auto;
    width: 175px;
